.advantages-block {
  margin: 85px auto 130px auto;
}
.advantages-block__title {
  color: #111111;
  font-family: "Montserrat", serif;
  font-weight: 800;
  font-size: 42px;
  text-align: center;
}
.advantages-block__description {
  max-width: 1100px;
  color: #030303;
  font-family: "Commissioner", serif;
  font-weight: 400;
  font-size: 20px;
  text-align: center;
  margin: 25px auto 42px auto;
}
.advantages-block__cards {
  display: flex;
  gap: 32px 43px;
  max-width: 1193px;
  flex-wrap: wrap;
  margin: 0 auto;
}


@media (min-width: 360px) and (max-width: 739px) {
  .advantages-block {
    margin: 40px auto 90px auto;
  }
  .advantages-block__title {
    font-size: 28px;
  }
  .advantages-block__description {
    max-width: 340px;
    font-size: 14px;
    margin: 15px auto 30px auto;
  }
  .advantages-block__cards {
    gap: 15px 0px;
    max-width: 340px;
  }
}

@media (min-width: 740px) and (max-width: 1199px) {
  .advantages-block {
    margin: 30px auto 90px auto;
  }
  .advantages-block__title {
    font-size: 32px;
  }
  .advantages-block__description {
    max-width: 640px;
    font-size: 15px;
    margin: 15px auto 30px auto;
  }
  .advantages-block__cards {
    gap: 15px 10px;
    max-width: 720px;
    margin: 0 auto;
    justify-content: center;
  }
}

@media (min-width: 1200px) and (max-width: 1439px)  {
  .advantages-block {
    margin: 50px auto 100px auto;
  }
  .advantages-block__title {
    font-size: 36px;
  }
  .advantages-block__description {
    max-width: 1000px;
    font-size: 17px;
    margin: 20px auto 35px auto;
  }
  .advantages-block__cards {
    gap: 15px 10px;
    max-width: 1000px;
    margin: 0 auto;
    justify-content: center;
  }
}

@media (min-width: 1440px) and (max-width: 1910px) {
  .advantages-block {
    margin: 50px auto 100px auto;
  }
  .advantages-block__title {
    font-size: 38px;
  }
  .advantages-block__description {
    font-size: 18px;
  }
  .advantages-block__cards {
    max-width: 1200px;
    margin: 0 auto;
    justify-content: center;
  }
}